import { Link } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Scrollbar, A11y, Autoplay } from "swiper";
import "swiper/swiper-bundle.min.css";

const HeaderSlider = () => {
  return (
    <div className="slider">
      <Swiper
        spaceBetween={50}
        slidesPerView={1}
        onSlideChange={() => console.log("slide change")}
        onSwiper={(swiper) => console.log(swiper)}
        navigation
        loop={true}
        speed={800}
        modules={[Navigation, Pagination, Scrollbar, A11y, Autoplay]}
      >
        <SwiperSlide>
          <div className="slider__item">
            <img src="images/content/slider/1.png" alt="slider" />
            <Link to={"/"}>
              <button className="slider__btn slider__btn-play" type="button">
                Play
              </button>
              <button className="slider__btn slider__btn-more" type="button">
                More info
              </button>
            </Link>
          </div>
        </SwiperSlide>

        <SwiperSlide>
          {" "}
          <div className="slider__item">
            <img src="images/content/slider/1.png" alt="slider" />
            <Link to={"/"}>
              <button className="slider__btn slider__btn-play" type="button">
                Play
              </button>
              <button className="slider__btn slider__btn-more" type="button">
                More info
              </button>
            </Link>
          </div>
        </SwiperSlide>
      </Swiper>
      {/* <div className="slider__items">
              <div className="slider__item">
                <img src="images/content/slider/1.png" alt="slider" />
                <Link to={"/"}>
                  <button
                    className="slider__btn slider__btn-play"
                    type="button"
                  >
                    Play
                  </button>
                  <button
                    className="slider__btn slider__btn-more"
                    type="button"
                  >
                    More info
                  </button>
                </Link>
              </div>
            </div> */}
    </div>
  );
};

export default HeaderSlider;
